<template>
  <section class="portfolio">
    <div class="container">
      <masonry-wall :items="pagedPosts" :ssr-columns="1" :column-width="360" :gap="16" v-if="!loading">
        <template #default="{ item, index }" :key="item.id">
          <portfolio-card :post="item"/>
        </template>
      </masonry-wall>
      <pre-loader v-else/>
      <v-pagination v-model="currentPage" :length="pageCount" rounded="circle"></v-pagination>
    </div>
  </section>
</template>

<script>
import PortfolioCard from "@/components/PortfolioCard";
import PreLoader from "@/components/PreLoader";

export default {
  name: "PortfolioPage",
  components: {PreLoader, PortfolioCard},

  data: () => ({}),

  created() {
    this.$store.dispatch('FETCH_POSTS');
  },

  computed: {
    pagedPosts() {
      return this.$store.getters['pagedPosts'];
    },
    loading() {
      return this.$store.state.loading;
    },
    currentPage: {
      get() {
        return this.$store.state.currentPage;
      },
      set(page) {
        this.$store.state.currentPage = page;
      }
    },
    pageCount() {
      return Math.ceil(this.$store.state.posts.length / this.$store.state.postsPerPage);
    }
  },
}
</script>

<style lang="scss">
.portfolio {
  padding: rem(50) 0;

  .masonry-wall {
    margin-bottom: rem(50);
  }

  .v-btn--size-default {
    font-size: rem(18);
  }
}


</style>
